@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  box-shadow: 5px 1px 5px 1px rgb(173, 196, 211);
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{animation-duration:.01ms !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
  color: grey;
}

a {
  text-decoration: none;
  color: rgb(24, 121, 124);
}

ul {
  list-style-type: none;
}

.flex--column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flex--row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

@media only screen and (max-width: 600px) {
  .Dashboard_main {
    display: flex;
    flex-direction: row;
  }
}

.Signin {
  width: auto;
  height: auto;
  margin-top: 100px;
}

.FormSignup {
  border: #00bfa6 1px solid;
  box-shadow: 5px 1px 2px 1px rgb(173, 196, 211);
  border-radius: 10px;
  width: 400px;
  height: 400px;
  margin-top: 50px;
}

.FormSignin {
  border: #00bfa6 1px solid;
  box-shadow: 5px 1px 2px 1px rgb(173, 196, 211);
  border-radius: 10px;
  width: 400px;
  display: flex;
  height: 200px;
}

.Home {
  justify-content: space-evenly;
  align-items: center;
}

.Home__img {
  width: 400px;
  height: 800px;
}

.FormJob {
  font-size: 16px;
  padding: 20px;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  border: azure 3px solid;
  color: darkgray;
  text-align: left;
}

.__label {
  color: blue;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0 4px;
  gap: 0 4px;
  grid-template-areas: ". . . . . . .";
  margin: 0 5px;
}

.contact {
  width: 100%;
  margin: 10px;
  padding: 10px;
  font-size: 12px;
}

.Dashboard__column {
  height: 100vh;
  background-color: #f5f4f4;
  flex-grow: 1;
  flex-wrap: wrap;
  justify-content: space-between;
  width: auto;
}

hr {
  border: rgb(173, 196, 211) solid 1px;
  box-shadow: 5px 1px 2px 1p rgb(199, 201, 202);
}

.Dashboard__column h3 {
  text-align: center;
  padding: 10px;
}
.Dashboard__search {
  justify-content: space-between;
  margin: 10px 5px;
}

.Dashboard__main {
  flex-grow: 1;
  flex-wrap: wrap;
  height: 100vh;
  width: auto;
}

.JobCard {
  border: #e9ebf2 2px solid;
  background-color: white;
  border-radius: 4px;
  margin-top: 8px;
  margin-bottom: 5px;
  padding: 10px;
  transition: 0.5s;
  align-content: center;
}

.JobCard__buttonDiv {
  display: flex;
  justify-content: space-around;
  width: auto;
}
.blueBtn {
  margin-top: 20px 0px;
  width: 80px;
  height: auto;
  padding: 0 5px;
  font-size: 14x;
  background-color: #bcf2e2;
  border: black 1px solid;
  border-radius: 5px;
  color: #282c33;
}

.FormSignin__btn {
  width: 100px;
  height: auto;
  padding: 0 5px;
  font-size: 14x;
  background-color: #bcf2e2;
  border: black 1px solid;
  border-radius: 5px;
  color: #282c33;
  margin-top: 10px;
}

#deleteBtn {
  width: 80px;
  height: auto;
  padding: 0 5px;
  font-size: 14x;
  border: black 1px solid;
  border-radius: 5px;
}
.JobCard h3 {
  color: #282c33;
  font: 16px;
  text-align: left;
  overflow-wrap: break-word;
}

.FormJobUpdate h1 {
  color: #282c33;
  font: 18px;
  text-align: left;
}

.JobCard h4 {
  color: #3c414d;
  font: 14px;
  overflow-wrap: break-word;
}

.FormJobUpdate h2 {
  color: #282c33;
  font: 16px;
  text-align: left;
}

.JobCard:hover {
  box-shadow: 5px 5px 5px 1px rgb(173, 196, 211);
  transform: scale(1);
}

.Profile {
  border-radius: 5px;
  border: 1px solid #00303f;
  height: 400px;
  width: auto;
  margin: 100px 300px;
  color: black;
  border: #e9ebf2 2px solid;
  border-radius: 4px;
  padding: 10px;
  text-align: left;
  display: flex;
  justify-content: space-evenly;
}

.NotFound img {
  margin-top: 40px;
  width: 50%;
  height: 70%;
}

.FormJob__form label {
  color: #3c414d;
}

.FormJob__form {
  width: 50vw;
}

.FormJob_form div {
  display: flex;
  flex-direction: column;
}

.FormJob_input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.FormUpdateUser {
  border: #00bfa6 1px solid;
  box-shadow: 5px 1px 2px 1px rgb(173, 196, 211);
  border-radius: 10px;
  margin-top: 100px;
}

.FormUpdateUser label {
  color: #00bfa6;
}

.FormJob_label {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.FormJobUpdate {
  width: 550px;
  height: auto;
  padding: 40px;
}

#jobDesc {
  white-space: pre-line;
}
s #notes {
  white-space: pre-line;
}

.FormJobUpdate label {
  color: #00bfa6;
}

.JobCard__buttonDiv {
  margin-top: 10px;
  width: 200px;
}

#updateDltBtn {
  margin: 10px 0;
}

.addMargin {
  margin-top: 10px;
}

#required {
  color: #a54b4a;
  font-size: 12px;
}

select {
  margin-bottom: 10px;
}

#detailImg {
  z-index: -1;
  height: 60vh;
  position: absolute;
  right: 0px;
  bottom: 0;
}

.topcard {
  left: 10px;
  top: 50px;
  z-index: 99;
  background-color: white;
  color: #00303f;
  text-align: center;
  margin: 50px;
}

